@charset "UTF-8";
body {
  font-family: "TT Lakes", sans-serif;
  margin: 0;
  background: #f8f8f8;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

.main {
  width: 100%;
}

button {
  padding: 0;
  border: none;
  background-color: transparent;
}

.header {
  height: 88px;
  display: flex;
  justify-content: space-between;
  margin: 0;
  align-items: center;
  background-color: #ffffff;
  width: 100vw;
}

.header__left {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-left: 16px;
}

.header__right {
  display: flex;
  justify-content: flex-end;
  gap: 16px;
  margin-right: 24px;
}

.header__right li:nth-child(4) {
  padding-top: 5px;
}

.services__title {
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  margin-top: 24px;
  margin-right: 16px;
  gap: 24px;
  font-size: 15px;
  font-weight: bold;
}

.services {
  display: flex;
  justify-content: space-between;
  margin: 0;
  align-items: center;
}

.services__actions {
  display: flex;
  gap: 16px;
  margin-right: 16px;
}

.services__actions button {
  display: flex;
  align-items: center;
  gap: 8px;
}

.navigate {
  display: flex;
  overflow: auto;
  text-align: center;
  gap: 16px;
  padding-left: 16px;
  flex-wrap: nowrap;
}

.navigate li {
  flex-shrink: 0;
  padding: 5px;
  border: 2px solid transparent;
  color: #7e7e82;
}

.navigate li:hover {
  cursor: pointer;
  border: 2px solid #41f6d7;
  border-radius: 3px;
  background-color: white;
}

.navigate::-webkit-scrollbar {
  display: none;
}

.content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 16px;
  margin-top: 24px;
}
.content__type {
  display: flex;
  flex-direction: column;
}
.content__next {
  display: flex;
  justify-content: flex-start;
  gap: 10px;
  margin-left: 10px;
}
.content__info {
  height: 94px;
  overflow: hidden;
}
.content__logo img {
  width: 100%;
  max-width: 320px; /* минимальная ширина изображения */
  transition: max-width 0.3s ease;
}
.content__info p {
  padding-left: 16px;
  padding-right: 16px;
  font-family: "TT Lakes Light", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.2px;
  color: #7e7e82;
}

.read-open {
  height: 220px;
}

@media (min-width: 0px) and (max-width: 767px) {
  .header__right li:nth-child(-n+4) {
    display: none;
  }
  .services__actions {
    display: none;
  }
}
@media (min-width: 768px) and (max-width: 1119px) {
  .services__actions {
    display: none;
  }
  .content {
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    width: 100%;
  }
  .content__info {
    height: 140px;
    overflow: hidden;
  }
  .content__logo img {
    min-width: 360px;
    margin-right: 16px;
    border-radius: 5px;
  }
  .read-open {
    height: 220px;
  }
}
@media (min-width: 1120px) and (max-width: 1439px) {
  .content {
    display: flex;
    flex-direction: row;
  }
  .content__info {
    overflow: hidden;
    height: 130px;
  }
  .content__logo img {
    min-width: 360px;
    margin-right: 16px;
    border-radius: 5px;
  }
  .services__actions {
    display: none;
  }
}
@media (min-width: 1440px) and (max-width: 3000px) {
  .header {
    display: none;
  }
  .navigate {
    flex-wrap: wrap;
  }
  .content {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .content__info {
    overflow: hidden;
    height: 200px;
  }
  .content__logo img {
    min-width: 530px;
    margin-right: 16px;
    border-radius: 5px;
  }
  .content__next {
    display: none;
  }
}
body {
  font-family: "TT Lakes", sans-serif;
  margin: 0;
  background: #f8f8f8;
}

button {
  padding: 0;
  border: none;
  background-color: transparent;
}

.aside {
  display: flex;
  flex-direction: column;
  width: 320px;
  height: unset;
  justify-content: space-between;
  position: static;
  padding: 18px 24px 16px 23px;
  box-sizing: border-box;
  background-color: #ffffff;
  flex-shrink: 0;
}

.aside__menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 40px;
}

.aside__left {
  display: flex;
  gap: 16px;
}

.aside__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.aside__contacts {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin: 0;
}

.aside__contacts ul {
  display: flex;
  flex-direction: row;
  gap: 16px;
}

.aside__contacts a {
  text-decoration: none;
  color: black;
}

.aside__navigation {
  display: flex;
  flex-direction: column;
  height: 100%;
  gap: 32px;
}

.aside__navigation li {
  position: relative;
}

.aside__navigation li::before {
  content: "";
  border-left: 4px solid transparent;
  height: 32px;
  position: absolute;
  top: -5px;
  left: -23px;
  border-bottom-right-radius: 50%;
  border-top-right-radius: 50%;
}

.aside__navigation li:hover::before {
  border-left: 4px solid #41f6d7;
}

.aside__contacts p:nth-child(3n) {
  font-weight: bold;
  font-size: 25px;
}

.aside__navigation li:hover {
  opacity: 0.6;
}

.aside__language li:hover {
  opacity: 0.6;
}

@media (width < 1440px) {
  .aside {
    position: fixed;
    top: 0;
    left: -320px;
    width: 320px;
    height: 100vh;
    background-color: #ffffff;
    z-index: 999;
    transition: left 0.3s ease;
    justify-content: flex-start;
    gap: 40px;
  }
}
.menu-open {
  left: 0;
  transition: left 0.3s ease;
}

@media (min-width: 768px) and (max-width: 1023px) {
  .aside {
    display: flex;
    justify-content: space-between;
  }
}
.wrapper {
  display: flex;
  justify-content: space-between;
}

